body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  background-color: #FAFAFA;
  color: #555970;
}
h1 {
  font-size: 20px;
  letter-spacing: 1px;
  color: #7474bf;
}
h2 {
  font-weight: lighter;
  margin-top: 55px;
}
h3 {
  font-weight: lighter;
  font-size: 30px;
  margin: 15px 0;
}
h4 {
  font-weight: lighter;
  font-size: 14px;
}
.logo {
  margin-left: 40px;
}
.links {
  margin: 0;
}
input.big-field {
  width: 380px;
  height: 38px;
  border-radius: 30px;
  border: none;
  margin-top: 30px;
  margin-bottom: 30px;
  /* Rectangle 4: */
  background: #FFFFFF;
  -webkit-box-shadow: 0 13px 40px 0 rgba(161,175,226,0.50);
          box-shadow: 0 13px 40px 0 rgba(161,175,226,0.50);
  outline: none;
  padding: 0 25px;
  font-size: 16px
}
input.big-button {
  color: white;
  width: 170px;
  height: 38px;
  border-radius: 30px;
  border: none;
  margin-top: 0;
  margin-bottom: 30px;
  /* Rectangle 4: */
  -webkit-box-shadow: 0 13px 40px 0 rgba(161,175,226,0.50);
          box-shadow: 0 13px 40px 0 rgba(161,175,226,0.50);
  outline: none;
  font-size: 16px;
  background: #7474BF;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #348AC7, #7474BF);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left top, right top, from(#348AC7), to(#7474BF));
  background: -webkit-linear-gradient(left, #348AC7, #7474BF);
  background: -o-linear-gradient(left, #348AC7, #7474BF);
  background: linear-gradient(to right, #348AC7, #7474BF); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  cursor: pointer;
}
input.big-button:hover {
  -webkit-filter: brightness(92%);
          filter: brightness(92%);
}
.width-control {
  max-width: 1000px;
  margin: 0 auto;
}
.logo {
  margin-top: 40px;
}
.tagline {
  text-align: center;
}
.long-url {
  text-decoration: none;
  color: #555970;
}
.short-url {
}
.domain {
  max-width: 300px;
  margin: 0 auto;
}
button {
    -ms-flex-align: start;
        align-items: flex-start;
    text-align: center;
    color: buttontext;
    background: none;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 0;
    margin: 0;
    border: none;
    -webkit-border-image: initial;
         -o-border-image: initial;
            border-image: initial;
    font-weight: 500;
    font-family: 'Roboto', sans-serif;
    color: #555970;
    cursor: crosshair;
}
button:focus {
    outline: none;
}
.footer {
  display: none;
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 1rem;
  /*background-color: #ECF0F8;*/
  text-align: center;
  font-size: 12px;
}



